import React, { FC } from 'react'
import { useNavigate } from 'react-router-dom'
import { Typography, Button, Box, ThemeProvider } from '@mui/material'
import serverError403 from '../../../assets/serverError403.png'
import serverError500 from '../../../assets/serverError500.png'
import serverError404 from '../../../assets/serverError404.png'
import theme from '../../../utils/themeMUI5'

type Props = {
  type: '500' | '403' | '404'
  title: string
  description: string
}

const getImage = (type: '500' | '403' | '404') => {
  switch (type) {
    case '500':
      return serverError500
    case '403':
      return serverError403
    case '404':
      return serverError404
    default:
      return undefined
  }
}

const ServerErrorComponent: FC<Props> = ({ type, title, description }) => {
  const navigate = useNavigate()
  const image = getImage(type)
  const onBackClick = () => {
    navigate('/')
  }

  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'space-between',
            alignItems: 'center',
            padding: '120px 144px',
            maxWidth: '1280px',
            minWidth: '1280px',
          }}
        >
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'start',
              alignItems: 'start',
              width: '286px',
              gap: '48px',
            }}
          >
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'start',
                alignItems: 'start',
                gap: '12px',
              }}
            >
              <Typography fontWeight={500} fontSize="128px" lineHeight="130px" color={theme.color.blueMain}>
                {title}
              </Typography>
              <Typography color={theme.color.black900} variant="body4">
                {description}
              </Typography>
            </Box>
            <Button sx={{ width: '285px' }} onClick={onBackClick}>
              Вернуться на главную
            </Button>
          </Box>
          <Box
            sx={{
              maxHeight: '540px',
              width: 'fit-content',
              objectFit: 'scale-down',
            }}
            component="img"
            src={image}
            alt={`ошибка сервера ${type}`}
          />
        </Box>
      </Box>
    </ThemeProvider>
  )
}

export default React.memo(ServerErrorComponent)
