import React, { useEffect, useMemo, useState } from 'react'
import { useDropzone } from 'react-dropzone'
import Button from '@material-ui/core/Button'
import { IconButton, Typography } from '@material-ui/core'
import { v4 as generateUniqueId } from 'uuid'
import { Box } from '@mui/system'
import useStyles from './Dropzone.style'
import IconDeleteButton from '../../../assets/Icons/IconDeleteButton/IconDeleteButton'
import {
  MAX_PORTFOLIO_SIZE,
  MAX_PORTFOLIO_SIZE_TIP,
  PORTFOLIO_COUNT_EXTRA_TIP,
  PORTFOLIO_COUNT_TIP,
  PORTFOLIO_SUPPORTED_FORMATS,
  PORTFOLIO_SUPPORTED_FORMATS_TIP,
} from '../../../utils/constants'

function Dropzone({
  filesState,
  deletedFiles = undefined,
  isEditPopup = undefined,
  label = undefined,
  setIsFilesChanged = undefined,
  maxFilesNumber = 2,
  nameTip = 'Назови файл в формате: Фамилия_Имя_Резюме',
  fileTip = 'Перетащи файлы: .pdf, .jpg, .jpeg, .png, не более 10 Мб.',
  error = undefined,
  acceptedFormats = undefined,
  isBigCover = false,
  isCover = false,
  isVacancyPopup = undefined,
}) {
  const classes = useStyles()
  const [fileError, setFileError] = React.useState(null)
  const [addedFiles, setAddedFiles] = filesState
  const [deletedId, setDeletedId] = deletedFiles || [0, 0]
  const countTip = PORTFOLIO_COUNT_TIP(maxFilesNumber)

  const maxSizeValidations = (file) => {
    if (file.size - MAX_PORTFOLIO_SIZE > 0) {
      return {
        code: 'file-too-large',
        message: MAX_PORTFOLIO_SIZE_TIP,
      }
    }
    return null
  }

  useEffect(() => {
    if (error) setFileError(error)
  }, [error])

  const { getRootProps, getInputProps, open } = useDropzone({
    maxFiles: maxFilesNumber,
    accept: acceptedFormats || PORTFOLIO_SUPPORTED_FORMATS,
    // Без этого пропса добавление файла будет открываться по пробелу и enter
    noKeyboard: true,
    // Отключение открытия окна добавления файлом кликом по дропзоне
    noClick: true,
    onDropAccepted: (files) => {
      if (isEditPopup) setIsFilesChanged(true)
      const fileListArr = Array.from(files)
      // Если уже имеется один загруженный файл, то заходим в условие
      if (addedFiles.length === 1) {
        // Если подгружаем один файл, то надо добавить его в стейт
        if (fileListArr.length === 1) {
          setAddedFiles([...addedFiles, ...fileListArr])
        } else {
          // Иначе возвращаем ошибку, говорим, что можно добавить только 1 файл
          setFileError(PORTFOLIO_COUNT_EXTRA_TIP)
        }
      } else {
        // а если файлов 0, то просто записываем в стейт
        setAddedFiles([...fileListArr])
      }
    },
    onDropRejected: (files) => {
      const errCode = files[0].errors[0].code
      switch (errCode) {
        case 'file-invalid-type': {
          setFileError(PORTFOLIO_SUPPORTED_FORMATS_TIP)
          break
        }
        case 'too-many-files': {
          setFileError(countTip)
          break
        }
        case 'file-too-large': {
          setFileError(files[0].errors[0].message)
          break
        }
        default: {
          break
        }
      }
    },
    onDrop: () => setFileError(null),
    validator: maxSizeValidations,
  })
  const handleRemoveFile = (e, index) => {
    e.preventDefault()
    if (index === -999) {
      setAddedFiles([])
      return
    }
    if (isEditPopup) {
      setIsFilesChanged(true)
      const newFilesState = [...addedFiles]
      // Достаём файл, который удаляем
      const deletedFile = newFilesState[index]
      // Если в нём есть поле id, то сохраняем его
      if (deletedFile.id) {
        setDeletedId([...deletedId, deletedFile])
      }
      newFilesState.splice(index, 1)
      setAddedFiles([...newFilesState])
    } else {
      const newFilesState = [...addedFiles]
      newFilesState.splice(index, 1)
      setAddedFiles([...newFilesState])
    }
  }
  const fileCounter = React.useMemo(() => {
    if (typeof addedFiles === 'string') return 1
    if (typeof addedFiles !== 'string' && !Array.isArray(addedFiles)) return 1
    return addedFiles?.length
  }, [addedFiles])

  const acceptedFileItems =
    typeof addedFiles === 'string' || !Array.isArray(addedFiles)
      ? useMemo(
          () => (
            <Box
              sx={{
                width: isCover ? 280 : 328,
                height: 152,
                objectFit: 'contain',
                position: 'relative',
              }}
            >
              <Box
                sx={{
                  width: '100%',
                  height: 152,
                  objectFit: 'contain',
                }}
                component="img"
                alt="пример обложки"
                src={typeof addedFiles === 'string' ? addedFiles : URL.createObjectURL(addedFiles)}
              />
              <IconButton className={classes.deleteButton} onClick={(e) => handleRemoveFile(e, -999)}>
                <IconDeleteButton />
              </IconButton>
            </Box>
          ),
          [addedFiles],
        )
      : useMemo(
          () =>
            addedFiles?.map((file, index) => {
              const objectUrl = isEditPopup ? undefined : URL.createObjectURL(file)
              const [fileName] = file.name.split('.')
              if (isCover || isBigCover) {
                return (
                  <Box
                    sx={{
                      width: isCover ? 280 : 328,
                      height: 152,
                      objectFit: 'contain',
                      position: 'relative',
                    }}
                  >
                    <Box
                      sx={{
                        width: '100%',
                        height: 152,
                        objectFit: 'contain',
                      }}
                      component="img"
                      alt="пример обложки"
                      src={objectUrl}
                    />
                    <IconButton className={classes.deleteButton} onClick={(e) => handleRemoveFile(e, index)}>
                      <IconDeleteButton />
                    </IconButton>
                  </Box>
                )
              }
              return (
                <li className={classes.resume} key={generateUniqueId()}>
                  <a href={file?.url} download className={classes.resume__link} target="_blank" rel="noreferrer">
                    <Typography className={classes.fileInfo} variant="body2">
                      <span style={{ color: 'hsla(218, 90%, 53%, 1)' }}>{index + 1}.&nbsp;</span>
                      {fileName}
                    </Typography>
                    <IconButton onClick={(e) => handleRemoveFile(e, index)}>
                      <IconDeleteButton />
                    </IconButton>
                  </a>
                </li>
              )
            }),
          [addedFiles],
        )

  const title = label || (
    <>
      Резюме (загружено <span style={{ color: 'hsla(218, 90%, 53%, 1)' }}>{fileCounter}</span> из 2 возможных файлов)
    </>
  )

  return (
    <Box
      sx={{
        // eslint-disable-next-line no-nested-ternary
        width: isBigCover ? '328px' : isCover ? '280px' : undefined,
        height: isBigCover || isCover ? '152px' : undefined,
      }}
      className={classes.container}
    >
      <Typography variant="body2" component="h2" className={classes.dropzoneTitle}>
        {title}
      </Typography>
      {fileCounter !== 0 && <ul className={classes.fileList}>{acceptedFileItems}</ul>}
      {fileCounter !== maxFilesNumber && (
        <div
          {...getRootProps({
            // eslint-disable-next-line no-nested-ternary
            className: isCover
              ? `${classes.dropzone} ${classes.dropzoneLittleCover}`
              : isBigCover
              ? `${classes.dropzone} ${classes.dropzoneCover}`
              : classes.dropzone,
          })}
          style={error && { border: '1px dashed #FF0200' }}
        >
          <input {...getInputProps({ className: classes.dropzoneInput })} />
          <div className={classes.dropzoneTextContainer}>
            {fileCounter !== 1 && !label && (
              <Typography variant="body2" className={classes.dropzoneText}>
                Несколько резюме можно загрузить для откликов на смежные позиции (например, Интернет-маркетолог и SMM
                менеджер).
              </Typography>
            )}
            {fileCounter !== 1 && (
              <Typography variant="body2" className={classes.dropzoneText}>
                {nameTip}
              </Typography>
            )}
            <Typography variant="body2" className={classes.dropzoneText}>
              {fileTip}
            </Typography>
          </div>
          <div className={classes.dropzoneButtonAndValidationContainer}>
            <Button className={classes.dropzoneButton} onClick={open}>
              Выбрать файл
            </Button>
            {fileError && <Typography className={classes.errorText}>{fileError}</Typography>}
          </div>
        </div>
      )}
    </Box>
  )
}

export default Dropzone
